import React, { Component } from 'react';
import { Badge, Dropdown, DropdownItem, DropdownMenu, DropdownToggle, Nav, NavItem, NavLink } from 'reactstrap';
import PropTypes from 'prop-types';
import { Modal, ModalBody, ModalFooter, ModalHeader, Button, Table, Input, Alert } from 'reactstrap'
import { AppAsideToggler, AppHeaderDropdown, AppNavbarBrand, AppSidebarToggler } from '@coreui/react';
import logo from '../../assets/img/brand/logo.svg'
import sygnet from '../../assets/img/brand/sygnet.svg'
import { loadLocalAccount, loadAccountInfo, getMobileOperatingSystem, saveUserSec, showQRDialog, createPeerId } from '../../views/ggv/js/gegeUtil.js'
import { FormGroup, Label, InputGroup, InputGroupAddon, InputGroupText, Card, CardBody, CardHeader, Collapse, } from 'reactstrap'
import BtbSecret from '../../views/conponents/BtbSecret/BtbSecret'
import DepositDialog from '../../views/conponents/DepositDialog/DepositDialog'
import classnames from 'classnames';
import { doBShopTransaction, loadBarCodeText } from '../../views/eshop/eshop.js'
import TransactionType from '../../views/conponents/TransactionType/TransactionType';
import AccountDialog from '../../views/conponents/AccountDialog/AccountDialog';
import PopHint from '../../views/conponents/PopHint/PopHint'

const propTypes = {
  children: PropTypes.node,
};

const defaultProps = {};

class DefaultHeader extends Component {

  constructor(props) {
    super(props);
    let random = new Date().getMilliseconds();
    let idx = random % 8 + 1;
    this.avatar = 'assets/img/avatars/' + idx + '.jpg';
    let act = { peerId: '', rsId: '', pk: '', balance: 0 }
    this.state = {
      account: act, danger: false, showServerList: false,
      newPeer: '', currentPeer: '', showQr: false, transfer: false, dialogMsg: null,
      hasCache: false, qrtab: '0', tranferOk: false, addServer: false,
      intro: false, accordion: [false, false, false, false],
    };
    this.fee = '1'
    this.deadline = '5';
  }

  toggleServerList() {
    this.setState({
      showServerList: !this.state.showServerList,
    });
  }

  toggleBarCode() {
    if (!this.state.showQr) {
      loadBarCodeText(((ok, bars) => {
        if (ok)
          this.setState({ bars })
      }).bind(this))
    }
    this.setState({ showQr: !this.state.showQr })
  }

  qrtabClicked(qrtab, qr) {
    this.setState({ qrtab })
    let div = document.querySelector('#qrcode');
    while (div.hasChildNodes()) {
      div.removeChild(div.lastChild);
    }
    showQRDialog(qr)
  }

  renderBarCodes() {
    if (!this.state.bars)
      return null
    let bars = this.state.bars;
    let content = bars.map((bar) => {
      return (<NavItem>
        <NavLink
          className={classnames({ active: this.state.qrtab === bar.tab })}
          onClick={() => { this.qrtabClicked(bar.tab, bar.qr); }}
        >
          <i className="icon-user"></i><Badge
            color="success">{bar.title} </Badge>
        </NavLink>
      </NavItem>)
    })
    return content;
  }

  toggleDanger() {
    this.setState({
      danger: !this.state.danger
    });
    if (!this.state.danger) {
      this.setState({ showQr: false })
    }
  }

  getCurrentPeer() {
    let cp = window.localStorage.getItem("cpeer");
    return cp;
  }

  savePeers(peers) {
    let data = '';
    for (let idx in peers) {
      let peer = peers[idx];
      data += peer + ";"
    }
    window.localStorage.setItem("btb-peers", data);
  }

  getCachedPeers() {
    let data = window.localStorage.getItem("btb-peers");
    let ps = [];
    if (data) {
      ps = data.split(";")
    }
    return ps;
  }

  checkMobileDevice() {
    if ('Android' === getMobileOperatingSystem()) {
      this.toggleAppInstallDialog()
    }
  }

  componentDidMount() {
    if (!sessionStorage.getItem('introdialog'))
      this.toggleIntroDialog();
    else {
      this.checkMobileDevice()
    }

    let currentPeer = this.getCurrentPeer()
    if (currentPeer)
      this.setState({ currentPeer });

    this.peers = this.getCachedPeers()
    //
    var account = loadLocalAccount();
    if (account) {
      this.setState({ account, hasCache: true });
      this.secret = account.secret;
      loadAccountInfo(account, (function (account) {
        if (account)
          this.setState({ account });
      }).bind(this))
    } else {
      this.setState({ hasCache: false })
    }
  }

  switchServer(peer) {
    console.log("switchServer " + peer);
    let currentPeer = peer;
    this.setState({ currentPeer });
    window.localStorage.setItem("cpeer", peer);
  }

  clickMore() {
    this.toggleDanger();
  }

  clearAccount() {
    window.localStorage.setItem("userSec", '');
    this.setState({ hasCache: false })
  }

  addServer() {
    console.log("add server " + this.state.newPeer);
    if (this.state.newPeer && this.state.newPeer.length > 5) {
      this.switchServer(this.state.newPeer)
      this.peers.push(this.state.newPeer);
      this.savePeers(this.peers);
      this.toggleServerList()
    }
  }

  renderPeers() {
    if (!this.peers)
      return null;
    let ps = this.peers.map((p) => {
      return (<DropdownItem onClick={e => { this.switchServer(p) }}>{p}</DropdownItem>)
    })
    return ps;
  }

  renderAddPeerBtn() {
    if (!this.state.newPeer || this.state.newPeer.length < 5)
      return null;
    return (<Button color="info" onClick={this.addServer.bind(this)}>+</Button>);
  }

  updateAccountInfo() {
    loadAccountInfo(this.state.account, (function (account) {
      if (account)
        this.setState({ account });
    }).bind(this))
  }

  renderNav() {
    return (<Nav className="d-md-down-none" navbar>
      <NavItem className="px-3">
        <NavLink href="/">Dashboard</NavLink>
      </NavItem>
      <NavItem className="px-3">
        <NavLink href="#">Users</NavLink>
      </NavItem>
      <NavItem className="px-3">
        <NavLink href="#">Settings</NavLink>
      </NavItem>
    </Nav>)
  }

  renderTransactionItem() {
    let content = this.state.account.transactions.map((res) => {
      let d = new Date();
      d.setTime(res.time)
      let btime = d.toLocaleString()
      return (<tr>
        <td><TransactionType type={res.type} /></td>
        <td><AccountDialog rsId={res.from} /></td>
        <td><AccountDialog rsId={res.to} /></td>
        <td>{res.amount} </td>
        <td>{res.fee} </td>
        <td>{btime}</td>
      </tr>)
    });

    return content;
  }

  renderRecentTransactions() {
    if (this.state.showQr)
      return null;
    if (this.state.account.transactions) {
      return (<div>
        <p>:</p>
        <Table hover bordered striped responsive size="sm">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {this.renderTransactionItem()}
          </tbody>
        </Table></div>)
    } else {
      return null;
    }
  }
  renderNews() {
    if (true)
      return null;
    return (<NavItem className="d-md-down-none">
      <a href="/#/news/editor" target='_blank'><i className="icon-share">Share News(,BTB</i></a>
    </NavItem>)
  }

  updateInvodeCode(e) {
    if (e && e.target) {
      let newPeer = e.target.value;
      this.setState({ newPeer })
    }
  }

  renderServerList() {
    if (!this.state.addServer)
      return null;
    return (<Dropdown isOpen={this.state.showServerList} toggle={() => {
      this.toggleServerList();
    }}>
      <DropdownToggle caret size="sm">
        (): {this.state.currentPeer}
      </DropdownToggle>
      <DropdownMenu right style={{ right: 'auto' }}>
        <DropdownItem header><Input style={{ width: 200 }} size='sm' type="text" id="name" placeholder="()" onChange={(this.updateInvodeCode).bind(this)} />{this.renderAddPeerBtn()}</DropdownItem>
        {this.renderPeers()}
      </DropdownMenu>
    </Dropdown>)
  }

  updateTSecret(e) {
    if (e && e.target) {
      let act = e.target.value;
      this.secret = act;
    }
  }

  updateFee(e) {
    if (e && e.target) {
      let v = e.target.value;
      if (v && v.length > 0)
        this.fee = v;
    }
  }
  updateDeadline(e) {
    if (e && e.target) {
      let act = e.target.value;
      this.deadline = act;
    }
  }
  updatePk(e) {
    if (e && e.target) {
      let act = e.target.value;
      this.pk = act;
    }
  }
  updateAmount(e) {
    if (e && e.target) {
      let act = e.target.value;
      this.amount = act;
    }
  }
  updateAccount(e) {
    if (e && e.target) {
      let act = e.target.value;
      this.rsId = act;
    }
  }

  doTransfer() {
    let senderpk = createPeerId(this.secret).pk;
    let data = {
      action: 'transaction', type: 'transfer', amount: this.amount,
      fee: this.fee, dealine: this.deadline, pk: senderpk, recipientPk: this.pk,
      recipientId: this.rsId
    };

    doBShopTransaction(this.secret, data, ((ok, msg) => {
      if (ok) {
        saveUserSec(this.secret)
        this.setState({ dialogMsg: 'success(),', tranferOk: true })
      } else {
        this.setState({ dialogMsg: 'err()' })
      }
    }).bind(this))
  }

  toggleTransfer() {
    if (this.state.danger) {
      this.toggleDanger()
    }
    this.setState({
      transfer: !this.state.transfer
    });
  }
  renderTransferDialog() {
    return (<Modal isOpen={this.state.transfer} toggle={this.toggleTransfer.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleTransfer.bind(this)}></ModalHeader>
      <ModalBody>
        <FormGroup>
          <div className="controls">
            <BtbSecret onSecret={(sec) => {
              if (sec) {
                this.secret = sec;
              }
            }} />
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>ID):</InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" type="text" onChange={this.updateAccount.bind(this)} placeholder='ID' />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>):</InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" type="text" onChange={this.updatePk.bind(this)} placeholder='' />
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText>):</InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" type="text" onChange={this.updateAmount.bind(this)} placeholder='()' />
              <InputGroupAddon addonType="append">
                <InputGroupText></InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        <FormGroup>
          <div className="controls">
            <InputGroup className="input-prepend">
              <InputGroupAddon addonType="prepend">
                <InputGroupText><PopHint id="min123" text='():' content=',' /></InputGroupText>
              </InputGroupAddon>
              <Input id="appendedPrependedInput" size="16" type="text" onChange={this.updateFee.bind(this)} placeholder='0.1' />
              <InputGroupAddon addonType="append">
                <InputGroupText></InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </FormGroup>
        {
          this.renderDeadline()
        }
        <FormGroup>
          {this.renderdialogMsg()}
        </FormGroup>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleTransfer.bind(this)}></Button>
        <Button color="danger" hidden={this.state.tranferOk} onClick={this.doTransfer.bind(this)}></Button>
      </ModalFooter>
    </Modal>)
  }

  renderDeadline() {
    if (true)
      return null;
    return (<FormGroup>
      <div className="controls">
        <InputGroup className="input-prepend">
          <InputGroupAddon addonType="prepend">
            <InputGroupText><PopHint id='dl4545' text='():' content='' /></InputGroupText>
          </InputGroupAddon>
          <Input id="appendedPrependedInput" size="16" onChange={this.updateDeadline.bind(this)} type="text" placeholder='5' />
          <InputGroupAddon addonType="append">
            <InputGroupText></InputGroupText>
          </InputGroupAddon>
        </InputGroup>
      </div>
    </FormGroup>)
  }

  renderdialogMsg() {
    if (this.state.dialogMsg) {
      return (<Label>{this.state.dialogMsg}</Label>)
    }
    return null;
  }

  renderBarCodeDialog() {
    return (<Modal isOpen={this.state.showQr} toggle={this.toggleBarCode.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleDanger.bind(this)}></ModalHeader>
      <ModalBody>
        <Nav tabs>
          {this.renderBarCodes()}
        </Nav>
        <div id='qrcode' />
        <p><a href='https://github.com/btbresource/btb-android'></a></p>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleBarCode.bind(this)}>OK</Button>
      </ModalFooter>
    </Modal>)
  }

  renderAccountDialog() {
    return (<Modal isOpen={this.state.danger} toggle={this.toggleDanger.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleDanger.bind(this)}></ModalHeader>
      <ModalBody>
        <p>ID: {this.state.account.rsId}</p>
        <p>:</p>
        <p>{this.state.account.pk}</p>
        <p>ID</p>
        <p> : {this.state.account.balance} </p>
        <p><Button color="info" onClick={e => this.toggleTransfer()}></Button></p>
        <p><a href='https://github.com/btbresource/btb-android'></a></p>
        {this.renderRecentTransactions()}
        <div id="qrcode" />
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleDanger.bind(this)}>OK</Button>
      </ModalFooter>
    </Modal>)
  }

  renderMoreBtn() {
    if (!this.state.hasCache) {
      return (<BtbSecret onSecret={(sec) => {
        if (sec) {
          this.secret = sec;
          this.setState({ hasCache: true })
        }
      }} />)
    }
    return (<div>
      <DropdownItem header tag="div" className="text-center"><Button color="info" onClick={this.toggleBarCode.bind(this)}></Button></DropdownItem>
      <DropdownItem header tag="div" className="text-center"><Button color="info" onClick={this.toggleIntroDialog.bind(this)}></Button></DropdownItem>
      <DropdownItem header tag="div" className="text-center"><Button color="info" onClick={this.clickMore.bind(this)}></Button></DropdownItem>
      <DropdownItem header tag="div" className="text-center"><Button color="info" onClick={this.clearAccount.bind(this)}></Button></DropdownItem>
    </div>)
  }

  toggleIntroDialog() {
    this.setState({
      intro: !this.state.intro,
    });
    sessionStorage.setItem('introdialog', 'shi')
  }

  toggleAppInstallDialog() {
    this.setState({
      appDialog: !this.state.appDialog,
    });
  }

  toggleAccordion(tab) {
    const prevState = this.state.accordion;
    const state = prevState.map((x, index) => tab === index ? !x : false);

    this.setState({
      accordion: state,
    });
  }

  renderIntroDialog() {
    return (<Modal isOpen={this.state.intro} toggle={this.toggleIntroDialog.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleIntroDialog.bind(this)}>--</ModalHeader>
      <ModalBody>
        <Card>
          <CardHeader>
            <small>,100%,8</small>
            <div className="card-header-actions">
              <Badge></Badge>
            </div>
          </CardHeader>
          <CardBody>
            <div id="accordion">
              <Card>
                <CardHeader id="headingOne">
                  <Button block color="link" className="text-left m-0 p-0" onClick={() => this.toggleAccordion(0)} aria-expanded={this.state.accordion[0]} aria-controls="collapseOne">
                    <h5 className="m-0 p-0"></h5>
                  </Button>
                </CardHeader>
                <Collapse isOpen={this.state.accordion[0]} data-parent="#accordion" id="collapseOne" aria-labelledby="headingOne">
                  <CardBody>
                    <div>1.,8</div>
                    <div>2..</div>
                    <div>3.50</div>
                    <div>4.</div>
                    <div>5.</div>
                    <div>6.</div>
                    <div>7.8</div>
                    <div>8.</div>
                  </CardBody>
                </Collapse>
              </Card>
              <Card>
                <CardHeader id="headingOne">
                  <Button block color="link" className="text-left m-0 p-0" onClick={() => this.toggleAccordion(1)} aria-expanded={this.state.accordion[1]} aria-controls="collapseOne">
                    <h5 className="m-0 p-0"></h5>
                  </Button>
                </CardHeader>
                <Collapse isOpen={this.state.accordion[1]} data-parent="#accordion" id="collapseOne" aria-labelledby="headingOne">
                  <CardBody>
                    <div>1.,100%</div>
                    <div>2.</div>
                    <div>3.</div>
                  </CardBody>
                </Collapse>
              </Card>
              <Card>
                <CardHeader id="headingOne">
                  <Button block color="link" className="text-left m-0 p-0" onClick={() => this.toggleAccordion(2)} aria-expanded={this.state.accordion[2]} aria-controls="collapseOne">
                    <h5 className="m-0 p-0"></h5>
                  </Button>
                </CardHeader>
                <Collapse isOpen={this.state.accordion[2]} data-parent="#accordion" id="collapseOne" aria-labelledby="headingOne">
                  <CardBody>
                    <div>1.</div>
                    <div>2.</div>
                  </CardBody>
                </Collapse>
              </Card>
            </div>
          </CardBody>
        </Card>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleIntroDialog.bind(this)}>OK</Button>
      </ModalFooter>
    </Modal>)
  }

  renderAppDownloadDialog() {
    return (<Modal isOpen={this.state.appDialog} toggle={this.toggleAppInstallDialog.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleAppInstallDialog.bind(this)}>--</ModalHeader>
      <ModalBody>
        <Card>
          <CardHeader>
            <small></small>
            <div className="card-header-actions">
              <Badge>BTB</Badge>
            </div>
          </CardHeader>
          <CardBody>
            <div id="accordion">
              <a href='https://github.com/btbresource/btb-android'></a>
            </div>
          </CardBody>
        </Card>
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleAppInstallDialog.bind(this)}>OK</Button>
      </ModalFooter>
    </Modal>)
  }

  renderDeposBtn() {
    if (!this.state.hasCache) {
      return (<Button color="danger" size='sm' onClick={this.toggleBarCode.bind(this)}>Help(</Button>)
    } else
      return (<DepositDialog />)
  }

  renderLoginBtn() {
    if (!this.state.hasCache) {
      return (<BtbSecret mode='simple' onSecret={(sec, act) => {
        if (sec) {
          this.setState({ hasCache: true, account: act })
        }
      }} />)
    } else {
      return (<AppHeaderDropdown direction="down">
        <DropdownToggle nav onClick={this.updateAccountInfo.bind(this)}>
          <img src={this.avatar} className="img-avatar" alt="admin@bshopping.cc" />
        </DropdownToggle>
        <DropdownMenu right style={{ right: 'auto' }}>
          <DropdownItem header tag="div" className="text-center"><strong></strong></DropdownItem>
          <DropdownItem header tag="div" className="text-center"><strong>{this.state.account.rsId}</strong></DropdownItem>
          <DropdownItem header tag="div" className="text-center"><strong> {this.state.account.balance} </strong></DropdownItem>
          {this.renderMoreBtn()}
        </DropdownMenu>
      </AppHeaderDropdown>)
    }
  }

  render() {

    // eslint-disable-next-line
    const { children, ...attributes } = this.props;

    return (
      <React.Fragment>
        <AppSidebarToggler className="d-lg-none" display="md" mobile />
        <AppNavbarBrand
          full={{ src: logo, width: 89, height: 25, alt: 'CoreUI Logo' }}
          minimized={{ src: sygnet, width: 30, height: 30, alt: 'CoreUI Logo' }}
        />
        <AppSidebarToggler className="d-md-down-none" display="lg" />

        {
          //this.renderNav()
        }
        {this.renderAccountDialog()}
        {
        //this.renderBarCodeDialog()
        }
        {this.renderTransferDialog()}
        {
          //this.renderIntroDialog()
        }
        {this.renderAppDownloadDialog()}
        <Nav className="ml-auto" navbar>
          {
            this.renderServerList()
          }
          {
            //this.renderNews()
          }
          <NavItem className="d-md-down-none">
            <NavLink href="#" onClick={(e) => {
              this.setState({ addServer: !this.state.addServer })
            }}><i className="icon-plus"></i></NavLink>
          </NavItem>
          <NavItem className="d-md-down-none">
            <NavLink href="#"><i className="icon-bell"></i><Badge pill color="danger">{this.state.account.balance}</Badge></NavLink>
          </NavItem>
          {
          //this.renderDeposBtn()
          }
          {this.renderLoginBtn()}
        </Nav>
        <AppAsideToggler className="d-md-down-none" />
        {/*<AppAsideToggler className="d-lg-none" mobile />*/}
      </React.Fragment>
    );
  }
}

DefaultHeader.propTypes = propTypes;
DefaultHeader.defaultProps = defaultProps;

export default DefaultHeader;
