import React, { Component } from 'react';
import { Popover, PopoverBody, PopoverHeader } from 'reactstrap';
import { getTransactionName } from '../../ggv/js/gegeUtil'

class TransactionType extends Component {
  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.state = {
      popoverOpen: false
    };
  }

  toggle() {
    this.setState({
      popoverOpen: !this.state.popoverOpen,
    });
  }

  getTransactionDetail(code) {
    var types = new Map();
    types.set("20-0", "")
    types.set("9-0", "10%")
    types.set("10-0", ",9999")
    types.set("8-0", ",0")
    types.set("8-4", "")
    types.set("0-0", "")
    types.set("80-0", "")
    types.set("80-1", "")
    types.set("80-1-80","")
    types.set("60-0", ",")
    types.set("60-1", ",")
    types.set("60-2", " 10%90%")
    types.set("60-3", "")
    types.set("60-3-2", ",")
    types.set("60-3-3", "")
    types.set("60-3-4", "")
    types.set("60-3-20", "8")
    types.set("60-3-21", "")
    types.set("60-4", "")
    types.set("60-4-30", ",")
    types.set("60-5", "")
    types.set("60-6", "")
    types.set("60-6-1", "3")
    types.set("60-6-2", "")
    types.set("60-6-3", "")
    types.set("60-6-4", "")
    types.set("60-6-22", "")
    types.set("60-7", "")
    types.set("60-7-1", "")
    types.set("60-7-2", "")
    types.set("60-7-3", "")
    types.set("60-8", "")
    types.set("60-8-40", "")
    types.set("60-8-50", "")
    let n = types.get(code)
    if (!n) {
      n = code;
    }
    return n;
  }

  render() {
    let type = this.props.type;
    let cotent = this.getTransactionDetail(type);
    let id = 'tpid' + this.props.id;
    let text = getTransactionName(type)
    return (
      <span>
        <span className='alert-primary  fade show' id={id} onClick={this.toggle}>
          {text}
        </span>
        <Popover placement='top' isOpen={this.state.popoverOpen} target={id} toggle={this.toggle}>
          <PopoverHeader>{text}</PopoverHeader>
          <PopoverBody>{cotent}</PopoverBody>
        </Popover>
      </span>
    );
  }
}
export default TransactionType;
